<template>
    <div class="wrap">
      <van-nav-bar
        title="申请信息"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />
      <div class="mainContent">
        <information :status="status"></information>
      </div>
    </div>
</template>

<script>
  import information from '../../../components/information'
    export default {
        name: "informationDetail",
      components: {
        information
      },
      data() {
          return {
            status: true
          }
      },
      methods: {
        onClickLeft(){
          this.$router.back();
        },
      }
    }
</script>

<style scoped>

</style>



